--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
  color: #414042;
  text-decoration: none; }
  a:visited {
    color: #414042; }
  a:hover, a:focus, a:active {
    color: #414042; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.main-navigation {
  margin-left: auto;
  margin-top: auto;
  padding: 10px 30px 10px 10px;
  transition: transform .5s ease-in-out; }
  .main-navigation ul {
    display: flex;
    align-items: flex-end;
    height: 100%;
    list-style: none;
    margin: 0;
    padding-left: 0; }
    .main-navigation ul li {
      font-size: 1em;
      padding: 0 5px;
      margin: 0 5px;
      position: relative;
      text-decoration: none;
      position: relative;
      white-space: nowrap; }
      .main-navigation ul li:after {
        content: "";
        position: absolute;
        left: -2px;
        bottom: 1px;
        height: 3px;
        background: white;
        width: calc(100% + 4px);
        transform: scaleX(0);
        transform-origin: left;
        transition: transform 0.2s ease-out;
        will-change: transform;
        z-index: -1; }
        .shrink .main-navigation ul li:after {
          background: #63c9c7; }
      .main-navigation ul li:hover:after, .main-navigation ul li.focus:after {
        transform: scaleX(1); }
    .main-navigation ul a {
      color: white;
      text-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
      font-size: 1.4rem;
      font-family: "quantico", sans-serif; }
      .shrink .main-navigation ul a, .single-graduate-profile .main-navigation ul a {
        color: #12828f;
        text-shadow: none; }
  .main-navigation a {
    display: block;
    text-decoration: none; }

/* Small menu. */
.menu-toggle {
  display: none; }

@media screen and (max-width: 700px) {
  .main-navigation {
    pointer-events: none;
    margin-top: 0;
    width: 100vw;
    position: absolute;
    z-index: 100;
    top: 0;
    right: 0;
    transition: background-color .2s ease-out;
    height: 100vh;
    transition-delay: .2s; }
    .main-navigation .menu-toggle {
      pointer-events: auto;
      display: block;
      position: absolute;
      top: 10px;
      right: 10px;
      z-index: 10;
      padding: 10px;
      border: none;
      background-color: transparent;
      cursor: pointer; }
      .main-navigation .menu-toggle span {
        display: block;
        height: 4px;
        width: 30px;
        margin-bottom: 4px;
        border-radius: 10px;
        background-color: white;
        box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
        transition: transform .25s ease-out; }
        .main-navigation .menu-toggle span:nth-child(3) {
          margin-bottom: 0; }
        .shrink .main-navigation .menu-toggle span {
          background-color: #f58f3b; }
    .main-navigation ul {
      pointer-events: auto;
      flex-direction: column;
      height: 100vh;
      padding: 40px 0 90px 0;
      justify-content: center;
      align-items: center;
      transform: translateX(100%);
      transition: transform .25s ease-out;
      transition-delay: .1s; }
      .main-navigation ul li {
        font-size: 1.6em;
        margin-bottom: 20px; }
        .shrink .main-navigation ul li:after {
          background-color: white; }
        .main-navigation ul li a {
          color: white;
          text-shadow: none; }
          .shrink .main-navigation ul li a, .single-graduate-profile .main-navigation ul li a {
            color: white; }
  .toggled.main-navigation {
    background-color: #63c9c7;
    transition-delay: 0;
    height: 100vh; }
    .toggled.main-navigation ul {
      transform: translateX(0);
      transition-delay: 0; }
    .toggled.main-navigation .menu-toggle {
      transform: rotate(-180deg);
      transition: transform .25s ease-in-out; }
      .toggled.main-navigation .menu-toggle span:nth-child(1) {
        transform: translateY(7px) rotate(45deg); }
      .toggled.main-navigation .menu-toggle span:nth-child(2) {
        transform: scaleX(0); }
      .toggled.main-navigation .menu-toggle span:nth-child(3) {
        transform: translateY(-9px) rotate(-45deg); } }

ul#footer-menu {
  list-style: none;
  margin: 0;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  ul#footer-menu li {
    font-size: 1rem;
