    position: relative; }
    ul#footer-menu li a {
      color: white; }
    ul#footer-menu li:after {
      content: "";
      position: absolute;
      left: -3px;
      bottom: 1px;
      height: 3px;
      background: #f58f3b;
      width: calc(100% + 6px);
      transform: scaleX(0);
