    opacity: 0;
    transition: opacity 0.5s linear; }
  #masthead.shrink, .single-graduate-profile #masthead {
    transition: background-color 0.5s linear; }
    @media screen and (min-width: 700px) {
      #masthead.shrink, .single-graduate-profile #masthead {
