        transform: translateZ(0) scale(1) translateY(40vh); } }
    .ready .hero-section .brand-mark {
      transition: transform 3s cubic-bezier(0, 1, 0, 1) 0.1s;
      transform: translateZ(-10000px) scale(101) translateY(-3vh); }

